          <v-btn outlined color="orange" v-bind="attrs" v-on="on">Reset dati</v-btn>
        </template>
        <v-card>
          <v-card-title>Reset dati</v-card-title>
          <v-card-text>
            Sei sicuro di volere resettare i dati?
