          <v-card-actions>
            <v-btn outlined color="#3B5985">NO, annulla</v-btn>
            <v-btn outlined color="#3B5985" style="margin-left:20px" @click="resetData">Sì, resetta</v-btn>
          </v-card-actions>
        </v-card>
      </v-menu>
      <v-btn outlined color="green" style="margin-left:20px" @click="$emit('show-pai')">Visualizza PAI</v-btn>
    </div>
