      <v-radio-group v-model="patient.gender.value" row>
        <v-radio label="Maschio" value="Maschio"></v-radio>
        <v-radio label="Femmina" value="Femmina"></v-radio>
      </v-radio-group>
    </div> -->
    <v-card v-if="cols == 4" elevation="2">
      <table style="width:100%">
        <tr>
          <td class="td-1 blocks-4">
            <DeviceData></DeviceData>
          </td>
          <td class="td-2 blocks-4">
            <AssessmentData :reset="reset"></AssessmentData>
          </td>
          <td class="td-3 blocks-4">
