    <!-- TABLE -->
    <v-card v-else-if="cols == 2" elevation="16">
      <table style="width:100%">
        <tr>
          <td class="td-1 blocks-2">
            <DeviceData></DeviceData>
          </td>
          <td class="td-2 blocks-2">
            <AssessmentData :reset="reset"></AssessmentData>
          </td>
        </tr>
        <tr>
          <td class="td-3 blocks-2">
            <DiagnosisData></DiagnosisData>
          </td>
          <td class="td-4 blocks-2">
            <PsysocialData></PsysocialData>
          </td>
        </tr>
      </table>
    </v-card>
    <!-- MOBILE -->
    <v-card v-else elevation="12">
      <table style="width:100%">
        <tr>
          <td class="td-1 blocks-1">
            <DeviceData></DeviceData>
          </td>
        </tr>
        <tr>
          <td class="td-2 blocks-1">
