<script>
import DeviceData from './DeviceData'
import AssessmentData from './AssessmentData'
import DiagnosisData from './DiagnosisData'
import PsysocialData from './PsysocialData.vue'

export default {
  name: 'DataCollection',
  props: {
    needs: Object
  },
  components: {
    DeviceData,
    AssessmentData,
    DiagnosisData,
    PsysocialData
  },
  computed: {
    isMobile() { return this.$store.getters.isMobile },
    window_width() { return this.$store.getters.window_width },
  },
  data: () => ({
    cols: 1,
    reset: false,
    patient: null
  }),
  watch: {
    window_width: function () {
      this.setListColumns()
    },
  },
  mounted() {
    this.setListColumns()
  },
  beforeMount() {
    this.patient = this.$pai.data.patient
  },
  methods: {
    resetData() {
      this.reset = !this.reset
      this.$pai.reset()
    },
    setListColumns() {
      const width = this.window_width
      if (width >= 1600) this.cols = 4
      else if (width < 1600 && width >= 800) this.cols = 2
      else this.cols = 1
    },
  },
};
</script>

