
const { isActive, getProblems, getObjectives, getStrategies } = require('./lib/Utils')

module.exports = function (need) {

    const data = require('../assets/needs/' + need + '.json')

    const module = {}

    module.isActive = isActive
    module.getResults = (pai_data) => {
        if (!pai_data || !isActive(pai_data, data.problems)) return null
        return {
            problems: getProblems(data.need, pai_data, data.problems),
            objectives: getObjectives(pai_data, data.objectives),
            strategies: getStrategies(pai_data, data.strategies)
        }

    }

    return module
}
