
const GeneratorClass = require('./Generator')

const data = require('../assets/conditions.json')

const reset = () => {
    data.patient.gender.value = null
    for (const item in data.presidi) {
        data.presidi[item].value = false
    }
    for (const item in data.scale) {
        data.scale[item].value = false
    }
    for (const item in data.diagnosi) {
        data.diagnosi[item].value = false
    }
    for (const item in data.psicosociale) {
        data.psicosociale[item].value = false
    }
}

module.exports = function () {

    const module = {}

    module.data = data

    module.reset = reset

    module.respirare = new GeneratorClass('respirare')
    module.alimentazione = new GeneratorClass('alimentazione')
    module.eliminazione = new GeneratorClass('eliminazione')
    module.igiene = new GeneratorClass('igiene')
    module.movimento = new GeneratorClass('movimento')
    module.riposo = new GeneratorClass('riposo')
    module.circolatorio = new GeneratorClass('circolatorio')
    module.sicurezza = new GeneratorClass('sicurezza')
    module.comunicazione = new GeneratorClass('comunicazione')
    module.terapia = new GeneratorClass('terapia')
    module.diagnostica = new GeneratorClass('diagnostica')

    return module
}
