<template>
  <fullscreen :fullscreen.sync="is_fullscreen">
    <v-app>
      <TheHeader></TheHeader>
      <router-view></router-view>
    </v-app>
  </fullscreen>
</template>

<script>
import TheHeader from '@/components/TheHeader'

export default {
  name: 'App',
  components: {
    TheHeader,
  },
  data: () => ({
    is_fullscreen: false,
  }),
  computed: {
    fullscreen() { return this.$store.getters.fullscreen },
  },
  watch: {
    fullscreen(val) {
      this.is_fullscreen = val
    }
  },
  created() {
    window.addEventListener("resize", this.resizeHandler)
  },
  destroyed() {
    window.removeEventListener("resize", this.resizeHandler)
  },
  mounted() {
    this.resizeHandler()
  },
  methods: {
    resizeHandler() {
      this.$store.dispatch('set_window_width', window.innerWidth)
      this.$store.dispatch('set_window_height', window.innerHeight)
    }
  },
};
</script>

<style>
html {
  /* overflow-y: hidden */
}

main {
  height: 0;
  margin: 0 0 0 0;
}
</style>