export default {
    state: () => ({
        fullscreen: false,
        window_width: 0,
        window_height: 0,
        isMobile: false,
    }),
    getters: {
        fullscreen(state) { return state.fullscreen },
        window_width(state) { return state.window_width },
        window_height(state) { return state.window_height },
        isMobile(state) { return state.isMobile },
    },
    actions: {
        toggle_fullscreen(context) {
            context.commit('set_fullscreen')
        },
        set_window_width(context, width) {
            context.commit('update_window_width', width)
        },
        set_window_height(context, height) {
            context.commit('update_window_height', height)
        }
    },
    mutations: {
        set_fullscreen(state) {
            state.fullscreen = !state.fullscreen
        },
        update_window_width(state, width) {
            state.window_width = width
            state.isMobile = width < 600 ? true : false
        },
        update_window_height(state, height) {
            state.window_height = height
        }
    },
}