<template>
  <div id="pai-generator-container" :style="{ height: (window_height - 70) + 'px' }">
    <DataCollection v-if="view" :needs="needs" @show-pai="dialog = true"></DataCollection>
    <v-dialog v-model="dialog" fullscreen persistent>
      <v-card>
        <v-card-title style="background-color: #3B5985;color: white">
          PAI generato<v-spacer></v-spacer>
          <span style="cursor: pointer;" @click="dialog = false">CHIUDI</span>
        </v-card-title>
        <v-tabs v-model="tab" align-with-title>
          <v-tabs-slider color="#3B5985"></v-tabs-slider>
          <v-tab>
            Area assistenziale
          </v-tab>
        </v-tabs>
        <v-tabs-items v-model="tab">
          <v-tab-item>
            <v-card>
              <GeneratedPai :needs="needs"></GeneratedPai>
            </v-card>
          </v-tab-item>
        </v-tabs-items>
      </v-card>
    </v-dialog>
    <div v-if="!view" style="height:100%;width:100%;margin-top: 20%;text-align: center;">
      <v-progress-circular :size="70" :width="7" color="green" indeterminate></v-progress-circular>
    </div>

  </div>
</template>

<script>
import DataCollection from '@/components/PaiGenerator/DataCollection'
import GeneratedPai from '@/components/PaiGenerator/GeneratedPai'
export default {
  components: {
    DataCollection,
    GeneratedPai,
  },
  name: 'PaiGenerator',
  computed: {
    window_height() { return this.$store.getters.window_height },
  },
  data: () => ({
    dialog: false,
    loader: true,
    view: true,
    pai_data: null,
    tab: 0,
    // needs
    needs: {
      respirare: null,
      alimentazione: null,
      eliminazione: null,
      igiene: null,
      movimento: null,
      riposo: null,
      circolatorio: null,
      sicurezza: null,
      comunicazione: null,
      terapia: null
    }
  }),
  watch: {
    pai_data: {
      deep: true,
      handler: function () {
        this.writePai()
      }
    },
  },
  beforeMount() {
    this.pai_data = this.$pai.data
  },
  mounted() { },

  methods: {
    writePai() {
      this.needs.respirare = this.$pai.respirare.getResults(this.pai_data)
      this.needs.alimentazione = this.$pai.alimentazione.getResults(this.pai_data)
      this.needs.eliminazione = this.$pai.eliminazione.getResults(this.pai_data)
      this.needs.igiene = this.$pai.igiene.getResults(this.pai_data)
      this.needs.movimento = this.$pai.movimento.getResults(this.pai_data)
      this.needs.riposo = this.$pai.riposo.getResults(this.pai_data)
      this.needs.circolatorio = this.$pai.circolatorio.getResults(this.pai_data)
      this.needs.sicurezza = this.$pai.sicurezza.getResults(this.pai_data)
      this.needs.comunicazione = this.$pai.comunicazione.getResults(this.pai_data)
      this.needs.terapia = this.$pai.terapia.getResults(this.pai_data)
    }
  }
};
</script>

<style scoped>
#pai-generator-container {
  overflow-y: auto;
  margin-top: 65px;
  width: 100%;
  padding: 10px;
}
</style>