<template>
    <v-card flat>
        <v-card-title>Obiettivi
            <v-btn @click="copyContent" :color="copy_success ? 'green' : 'black'" outlined x-small
                style="margin-left:10px">{{ !copy_success ? 'COPIA' : 'COPIATO!' }}</v-btn>
            <!-- <v-btn @click="init" small style="margin-left:10px">RIGENERA</v-btn> -->
        </v-card-title>
        <v-card-text>
            <div v-html="html"></div>
            <!-- <v-textarea v-model="text" auto-grow>{{ text }}</v-textarea> -->
        </v-card-text>
    </v-card>
</template>

<script>
export default {
    name: 'GeneratedObjectives',
    props: {
        needs: Object
    },
    data: () => ({
        copy_success: false,
        text: ``,
        html: ``,
    }),
    watch: {
        needs: {
            deep: true,
            handler: function () {
                this.init()
            }
        },
    },
    mounted() {
        this.init()
    },
    methods: {
        init() {
            let text = ''
            if (this.needs.respirare) text += `${this.needs.respirare.objectives}\n\n`
            if (this.needs.alimentazione) text += `${this.needs.alimentazione.objectives}\n\n`
            if (this.needs.eliminazione) text += `${this.needs.eliminazione.objectives}\n\n`
            if (this.needs.igiene) text += `${this.needs.igiene.objectives}\n\n`
            if (this.needs.movimento) text += `${this.needs.movimento.objectives}\n\n`
            if (this.needs.riposo) text += `${this.needs.riposo.objectives}\n\n`
            if (this.needs.circolatorio) text += `${this.needs.circolatorio.objectives}\n\n`
            if (this.needs.sicurezza) text += `${this.needs.sicurezza.objectives}\n\n`
            if (this.needs.comunicazione) text += `${this.needs.comunicazione.objectives}\n\n`
            if (this.needs.terapia) text += `${this.needs.terapia.objectives}\n\n`

            let html = ''
            if (this.needs.respirare) html += `<p>${this.needs.respirare.objectives}</p>`
            if (this.needs.alimentazione) html += `<p>${this.needs.alimentazione.objectives}</p>`
            if (this.needs.eliminazione) html += `<p>${this.needs.eliminazione.objectives}</p>`
            if (this.needs.igiene) html += `<p>${this.needs.igiene.objectives}</p>`
            if (this.needs.movimento) html += `<p>${this.needs.movimento.objectives}</p>`
            if (this.needs.riposo) html += `<p>${this.needs.riposo.objectives}</p>`
            if (this.needs.circolatorio) html += `<p>${this.needs.circolatorio.objectives}</p>`
            if (this.needs.sicurezza) html += `<p>${this.needs.sicurezza.objectives}</p>`
            if (this.needs.comunicazione) html += `<p>${this.needs.comunicazione.objectives}</p>`
            if (this.needs.terapia) html += `<p>${this.needs.terapia.objectives}</p>`


            this.text = text
            this.html = html
        },
        async copyContent() {
            try {
                await navigator.clipboard.writeText(this.text);
                this.copy_success = true
                setTimeout(() => {
                    this.copy_success = false
                }, 2000);
            } catch (err) {
                console.error('Failed to copy: ', err);
            }
        }
    }
}

</script>
<style scoped>
.spacer {
    margin-top: 8px;
}
</style>