const isActive = (pai_data, problems) => {
    let active = problems.length == 0 ? true : false
    problems.forEach(i => {
        let is_true = true
        for (const key in i) {
            const spl = key.split('.')
            if (pai_data[spl[0]][spl[1]].value != i[key]) is_true = false
        }
        if (is_true) active = true
    })
    return active
}

const getProblems = (need, pai_data, problems) => {
    let text = need
    if (problems.length > 0) text += ' correlato a '
    problems.forEach(i => {
        for (const key in i) {
            const spl = key.split('.')
            if (pai_data[spl[0]][spl[1]].value) text += pai_data[spl[0]][spl[1]].text + ', '
        }
    })
    return problems.length == 0 ? text : text.substring(0, text.length - 2) + '.'
}

const getObjectives = (pai_data, objectives) => {
    let text = ''
    objectives.forEach(o => {
        let active = false
        if (o.conditions.length > 0) {
            o.conditions.forEach(i => {
                let is_true = true
                for (const key in i) {
                    const spl = key.split('.')
                    if (pai_data[spl[0]][spl[1]].value != i[key]) is_true = false
                }
                if (is_true) {
                    if (!active) text += o.text + '. '
                    active = true
                }
            })
        } else {
            text += o.text + '. '
        }
    })
    return text.substring(0, text.length - 1)
}

const getStrategies = (pai_data, strategies) => {
    let text = ''
    strategies.forEach(o => {
        let active = false
        if (o.conditions.length > 0) {
            o.conditions.forEach(i => {
                let is_true = true
                for (const key in i) {
                    const spl = key.split('.')
                    if (pai_data[spl[0]][spl[1]].value != i[key]) is_true = false
                }
                if (is_true) {
                    if (!active) text += o.text + '. '
                    active = true
                }
            })
        } else {
            text += o.text + '. '
        }
    })
    return text.substring(0, text.length - 1)
}



module.exports = { isActive, getProblems, getObjectives, getStrategies }
